<template>
  <div>
    <div class="banner">
      <router-link to="/vip">
        <img src="../../assets/img/seach-banner.png" alt />
      </router-link>
    </div>
    <div class="w1024">
      <div class="title">
        <ul>
          <li @click="clickActive(0)">找到项目</li>
          <li @click="clickActive(1)">找到资金</li>
          <li
            style="flex: 1;text-align: right;"
          >共找到项目:{{data.projects.total}} 个 资金:{{data.moneys.total}} 个</li>
        </ul>
        <div id="bar" class="bar"></div>
      </div>
      <div class="list" v-show="active==0">
        <ul class="list_ul">
          <router-link
            tag="li"
            :to="`/project/detail/${item.id}`"
            v-for="(item, index) in data.projects.rows"
            :key="index"
          >
            <div class="left">
              <img :src="imgUrl+item.logo" alt />
            </div>
            <div class="right">
              <p>{{item.name}}</p>
              <p>
                <span>标签:</span>
                {{item.label}}
              </p>
              <p>
                <span>介绍:</span>
                {{item.introduce}}
              </p>
            </div>
          </router-link>
          <li v-if="data.projects.total == 0" style="justify-content: center;">没有找到任何数据</li>
        </ul>
        <div class="page">
          <Page :total="data.projects.total" @on-change="pageChange" show-elevator />
        </div>
      </div>
      <div class="list" v-show="active==1">
        <ul class="list_ul">
          <router-link
            tag="li"
            :to="`/capital/detail/${item.id}`"
            v-for="(item, index) in data.moneys.rows"
            :key="index"
          >
            <div class="left">
              <img :src="imgUrl+item.logo" alt />
            </div>
            <div class="right">
              <p>{{item.institutions}}</p>
              <p>
                <span>领域:</span>
                {{item.territory}}
              </p>
              <p>
                <span>介绍:</span>
                {{item.dyf}}
              </p>
            </div>
          </router-link>
          <li v-if="data.moneys.total == 0" style="justify-content: center;">没有找到任何数据</li>
        </ul>
        <div class="page">
          <Page :total="data.moneys.total" @on-change="pageChange" show-elevator />
        </div>
      </div>
    </div>
    <br />
    <br />
  </div>
</template>
<script>
import { Page } from "iview";
import { homeSearch } from "../../api/public";
import { imgUrl, messages } from "../../utils";
import Velocity from "velocity-animate";
export default {
  components: {
    [Page.name]: Page
  },
  data() {
    return {
      //搜索配置
      seachConfig: {
        pageNo: 1, //当前页
        pageSize: 10, //每页条数
        s: this.$route.query.data
      },
      active: 0,
      data: {
        projects: {
          total: 0
        },
        moneys: {
          total: 0
        }
      },
      imgUrl
    };
  },
  methods: {
    getData() {
      homeSearch(this.seachConfig).then(res => {
        this.data = res.data.data;
        if (this.data.moneys.total == 0 && this.data.projects.total == 0) {
          messages.warning("没有找到任何项目/资金数据");
        } else {
          messages.success(
            `共找到相关项目: ${this.data.projects.total} 个 资金: ${this.data.moneys.total} 个`
          );
        }
      });
    },
    //分页点击
    pageChange(page) {
      this.seachConfig.pageNo = page;
      this.getData();
    },
    clickActive(active) {
      this.active = active;
      Velocity(document.getElementById("bar"), "stop");
      Velocity(
        document.getElementById("bar"),
        {
          left: active == 0 ? "20px" : "125px"
        },
        {
          easing: "easeInOutCirc",
          duration: 400
        }
      );
    }
  },
  created() {
    if (this.$route.query.data) {
      this.getData();
    }
  },
  watch: {
    $route(to, from) {
      if (this.$route.query.data) {
        this.seachConfig.pageNo = 1;
        this.seachConfig.s = this.$route.query.data;
        this.getData();
      }
    }
  }
};
</script>
<style lang="less" scoped>
.banner {
  margin: 0 auto;
  width: 1024px;
  padding: 20px 0px;
  img {
    width: 1024px;
  }
}

.w1024 {
  width: 1024px;
  margin: 0 auto;
  border: 1px solid #e6e6e6;
  .title {
    border-bottom: 1px solid #e6e6e6;
    position: relative;
    .bar {
      position: absolute;
      background: #f55231;
      width: 70px;
      height: 4px;
      bottom: -1px;
      left: 20px;
    }
    ul {
      display: flex;
      li {
        cursor: pointer;
        padding: 20px 0px;
        margin: 0 30px;
      }
      //   .active {
      //     border-bottom: 2px solid #f55231;
      //   }
    }
  }
  .list {
    .list_ul {
      display: flex;
      flex-flow: column;
      li {
        padding: 30px;
        border-bottom: 1px solid #e6e6e6;
        display: flex;
        cursor: pointer;
        .left {
          img {
            width: 74px;
            height: 74px;
            margin-right: 30px;
          }
        }
        .right {
          flex-grow: 1;
          display: flex;
          flex-flow: column;
          justify-content: center;
          p {
            padding: 5px 0;
            font-size: 12px;
            color: #999999;
            span {
              color: #f55231;
              margin-right: 5px;
            }
          }
          p:nth-child(1) {
            font-size: 16px;
            color: #333333;
          }
          p:nth-child(3) {
            word-break: break-all;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
            overflow: hidden;
            height: 17px;
          }
        }
      }
    }
  }
}
.page {
  padding: 20px 0px;
  text-align: center;
}
</style>